import React, { useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
import { PieChart, Pie, Sector, Cell, ResponsiveContainer } from 'recharts';
import useStyles from "./styles"
import { useTheme } from "@material-ui/core/styles";
import Widget from "../Common/Widget/Widget"
import Dot from "../Common/Dot"


export default function StatItem({ data }) {
    const classes = useStyles();
    const theme = useTheme();

    const PieChartData = [
        { name: "Landed      ", value: data.landed, color: "success" },
        { name: "Free        ", value: data.free, color: "primary" },
        { name: "Running     ", value: data.running, color: "warning" },
        { name: "Did not land", value: data.not_landed, color: "secondary" },
    ];
    return (
        <Widget title={data.name} upperTitle className={classes.card}>
            <Grid container spacing={2}>
                <Grid item xs={6}>
                    <ResponsiveContainer width="100%" height={144}>
                        <PieChart>
                            <Pie
                                data={PieChartData}
                                innerRadius={30}
                                outerRadius={40}
                                dataKey="value"
                            >
                                {PieChartData.map((entry, index) => (
                                    <Cell
                                        key={`cell-${index}`}
                                        fill={theme.palette[entry.color].main}
                                    />
                                ))}
                            </Pie>
                        </PieChart>
                    </ResponsiveContainer>
                </Grid>
                <Grid item xs={6}>
                    <div className={classes.pieChartLegendWrapper}>
                        {PieChartData.map(({ name, value, color }, index) => (
                            <div key={color} className={classes.legendItemContainer}>
                                <Dot color={color} />
                                <span className="region-state-wrapper">
                                    <span className="region-state" >
                                        {name}
                                    </span>
                                    <span className="region-state-value">
                                        {value}
                                    </span>
                                </span>
                            </div>
                        ))}
                    </div>
                </Grid>
            </Grid>
        </Widget>
    );
}